/* Navbar */
@mixin menu-style {
	@include media-breakpoint-up(md) {
		position: absolute;
		top: 77px;
		z-index: 999;
		background: $content-bg;
		border-top: none;
		border-radius: 0px;
		box-shadow: 1px 0px 20px rgba(0, 0, 0, 0.4);
		-webkit-box-shadow: 1px 0px 20px rgba(0, 0, 0, 0.4);
		li {
			a { 
				position: relative;
				padding-left: 20px;
				font-weight: 400;
				font-size: 15px;
				&::before {
					position: absolute;
					content: "";
					width: 8px;
					height: 8px;
					border-radius: 100%;
					border: 1px solid theme-color(light);
					top: 12px;
					left: 0;
				}	
				&:hover {
					&::before {
						background: theme-color(primary);
						border: 1px solid theme-color(primary);
					}	
				}		
			}
		}
	}
	@include media-breakpoint-down(md) {
		position: relative;
		top:0;
		box-shadow: none;
	}
}

@mixin show-submenu {
	.submenu {
		display: block;
	}

	> .nav-link {
		background: $horizontal-bottom-menu-item-hover-bg;
		i,
		.menu-title {
			color: $horizontal-bottom-menu-item-hover-color;
			transition: color 1s ease;
		}
		.menu-arrow {
			@include transform(rotate(-180deg));
			@include transform-origin(center);
            @include transition-duration($action-transition-duration);
		}
	}
}